import { Button, TextField } from "@mui/material";
import { useState, useContext } from "react";
import { useForm } from "react-hook-form";
import { useNavigate } from "react-router-dom";
import UserModel from "../../Models/UserModel";
import vacationService from "../../Utils/VacationServices";
import "./Login.css";

function Login(): JSX.Element {
  const navigate = useNavigate();
  const { register, handleSubmit } = useForm<UserModel>();
  const [user, setData] = useState<UserModel>();

  async function send(user: UserModel) {
    try {
      if (
        user.username === "Admin" ||
        (user.username === "admin" && user.password === "password")
      ) {
        navigate("/admin/vacations");
      } else {
        await vacationService.login(user).then((res) => {
          if (res) {
            vacationService.getAllUsers().then((res) =>
              res.forEach((userFromList) => {
                if (userFromList.username === user.username) {
                  vacationService
                    .getSingleUser(userFromList.user_id)
                    .then(() => navigate("/vacations"));
                }
              })
            );
          } else {
            alert("Incorrect username or password");
          }
        });
      }
    } catch (err: any) {
      alert("incorrect username or password");
    }
  }

  return (
    <div className="Login">
      <form onSubmit={handleSubmit(send)}>
        <h1>Sign In</h1>
        <TextField
          fullWidth
          id="standard-basic"
          label="Username"
          {...register("username")}
          value={user?.username}
        />
        <br />
        <br />
        <TextField
          type="password"
          fullWidth
          id="standard-basic"
          label="Password"
          {...register("password")}
          value={user?.password}
        />
        <Button type="submit" fullWidth variant="contained" color="primary">
          Sign In{" "}
        </Button>
      </form>
      <div className="sign-up">
        <label>Don't have an account? </label>
        <Button
          fullWidth
          variant="contained"
          color="primary"
          onClick={() => {
            navigate("/sign_up/");
          }}
        >
          Sign Up{" "}
        </Button>
      </div>
    </div>
  );
}

export default Login;
