import axios from "axios";
import { log } from "console";
import UserModel from "../Models/UserModel";
import VacationModel from "../Models/VacationModel";
import appUrl from "./Config";

class Services {
  public async getAllVacations(): Promise<VacationModel[]> {
    const response = await axios.get<VacationModel[]>(appUrl.vacations);
    return response.data;
  }

  public async getAllUsers(): Promise<UserModel[]> {
    const response = await axios.get<UserModel[]>(appUrl.users);
    return response.data;
  }

  public async getSingleVacation(vacation_id: number): Promise<VacationModel> {
    const response = await axios.get<VacationModel[]>(
      appUrl.singleVacation + vacation_id
    );
    console.log(response.data);
    return response.data[0];
  }

  public async getSingleUser(user_id: number): Promise<UserModel> {
    const response = await axios.get<UserModel[]>(appUrl.singleUser + user_id);
    return response.data[0];
  }

  //USER ACTIONS
  public async signUp(user: UserModel): Promise<void> {
    const response = await axios.post<UserModel>(appUrl.signUp, user);
  }

  public async login(user: UserModel): Promise<boolean> {
    console.log("user in here", user);

    const response = await axios.post<UserModel>(appUrl.login, user);
    console.log("res", response);
    if (response.status === 202) {
      return false;
    } else {
      return true;
    }
  }

  public async followVacation(
    vacation: VacationModel,
    vacation_id: number
  ): Promise<void> {
    const response = await axios.put<VacationModel>(
      appUrl.followVacation + vacation_id,
      vacation
    );
  }

  public async unfollowVacation(
    vacation: VacationModel,
    vacation_id: number
  ): Promise<void> {
    const response = await axios.put<VacationModel>(
      appUrl.unfollowVacation + vacation_id,
      vacation
    );
  }

  //ADMIN ACTIONS
  public async deleteVacation(vacation_id: number): Promise<void> {
    const response = await axios.delete<VacationModel>(
      appUrl.removeVacation + vacation_id
    );
  }

  public async addVacation(vacation: VacationModel): Promise<void> {
    const response = await axios.post<VacationModel>(
      appUrl.addVacation,
      vacation
    );
  }

  public async updateVacation(
    vacation: VacationModel,
    vacation_id: number
  ): Promise<void> {
    const response = await axios.put<VacationModel>(
      appUrl.updateVacation + vacation_id,
      vacation
    );
  }
}

const vacationService = new Services();
export default vacationService;
