import UserModel from "../../Models/UserModel";
import { useEffect, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import { useForm } from "react-hook-form";
import vacationService from "../../Utils/VacationServices";
import "./SignUp.css";
import { Button, TextField } from "@mui/material";

function SignUp(): JSX.Element {
  const [users, setUser] = useState<UserModel[]>([]);
  const { register, handleSubmit } = useForm<UserModel>();
  const [user, setData] = useState<UserModel>();

  const navigate = useNavigate();
  const params = useParams();
  const id = +(params.id || "");

  async function send(user: UserModel) {
    try {
      await vacationService.signUp(user);
      navigate("/vacations/");
    } catch (err: any) {
      alert("username already taken");
    }
  }

  const getSingleData = async (user_id: number) => {
    let myData = await vacationService.getSingleUser(user_id);
    console.log(myData);
    setData(myData);
  };

  useEffect(() => {
    if (id > 0) {
      getSingleData(id);
    }
    vacationService
      .getAllUsers()
      .then((users) => setUser(users))
      .catch((err) => alert(err.message));
  }, []);

  return (
    <div className="SignUp">
      <form onSubmit={handleSubmit(send)}>
        <h1>Sign Up</h1>
        {/* <label>First Name</label>
        <input
          type="text"
          {...register("first_name")}
          value={user?.first_name}
          placeholder="First Name"
          required
        /> */}

        <TextField
          fullWidth
          id="standard-basic"
          {...register("first_name")}
          value={user?.first_name}
          placeholder="First Name"
          required
        />
        <br />
        <br />
        <TextField
          fullWidth
          id="standard-basic"
          {...register("last_name")}
          value={user?.last_name}
          placeholder="Last Name"
          required
        />
        <br />
        <br />
        <TextField
          fullWidth
          id="standard-basic"
          maxLength={20}
          {...register("username")}
          value={user?.username}
          placeholder="Username"
          required
        />
        <br />
        <br />

        <TextField
          type="password"
          fullWidth
          id="standard-basic"
          minLength={8}
          maxLength={30}
          {...register("password")}
          value={user?.password}
          placeholder="Password"
          required
        />

        <Button type="submit" fullWidth variant="contained" color="primary">
          Sign Up{" "}
        </Button>
      </form>
    </div>
  );
}

export default SignUp;
