import { useEffect, useState, useContext } from "react";
import "./Vacations.css";
import vacationService from "../../Utils/VacationServices";
import VacationModel from "../../Models/VacationModel";
import { Pagination } from "@mui/material";

function Vacations(): JSX.Element {
  const [vacations, setData] = useState<VacationModel[]>([]);

  vacations.sort((a, b) => {
    return new Date(a.date_from).valueOf() - new Date(b.date_from).valueOf();
  });

  //

  //
  useEffect(() => {
    vacationService
      .getAllVacations()
      .then((response) => {
        setData(response);
      })
      .then(() => console.log(vacations))
      .catch((err) => alert(err.message));
  }, []);

  return (
    <div className="Vacations">
      <h1>Vacations</h1>
      <div className="allVacations">
        {vacations.map((item) => (
          <div className="vacation" key={item.vacation_id}>
            <>
              <div className="image">
                <img src={item.image} />
              </div>
              <br />
              <div className="destination">{item.destination}</div>
              <div className="description">{item.description}</div>
              <div className="dates">
                {item.date_from?.toLocaleString().split("T")[0]} to{" "}
                {item.date_to?.toLocaleString().split("T")[0]}
              </div>
              <div className="price">${item.price}</div>
              <div className="followers">
                <span className="following">❤️</span>
                {item.followers}
              </div>
            </>
          </div>
        ))}
      </div>
      <Pagination count={10} />
    </div>
  );
}

export default Vacations;
