import "./AdminVacations.css";
import axios from "axios";
import { useEffect, useState } from "react";
import VacationModel from "../../../Models/VacationModel";
import vacationService from "../../../Utils/VacationServices";
import { useNavigate } from "react-router-dom";

function AdminVacations(): JSX.Element {
  const [vacations, setData] = useState<VacationModel[]>([]);
  const navigate = useNavigate();

  useEffect(() => {
    vacationService
      .getAllVacations()
      .then((response) => {
        setData(response);
      })
      .then(() => console.log(vacations))
      .catch((err) => alert(err.message));
  }, []);

  return (
    <div className="AdminVacations">
      <h1>Hello, Admin</h1>
      <button
        onClick={() => {
          navigate("/admin/new_vacation/0");
        }}
      >
        ➕
      </button>
      <div className="allVacations">
        {vacations.map((item) => (
          <div className="vacation" key={item.vacation_id}>
            <>
              <div className="image">
                <img src={item.image} />
              </div>
              <div className="destination">{item.destination}</div>
              <div className="description">{item.description}</div>
              <div className="dates">
                {item.date_from?.toLocaleString().split("T")[0]} to{" "}
                {item.date_to?.toLocaleString().split("T")[0]}
              </div>
              <div className="price">${item.price}</div>
              <div className="followers">🖤{item.followers}</div>
              <div className="actions">
                <button
                  className="edit"
                  onClick={() => {
                    navigate("/admin/new_vacation/" + item.vacation_id);
                  }}
                >
                  ✏️
                </button>
                <button
                  className="remove"
                  onClick={() => {
                    vacationService.deleteVacation(item.vacation_id);
                    setData(
                      vacations.filter(
                        (singleItem) =>
                          item.vacation_id != singleItem.vacation_id
                      )
                    );
                  }}
                >
                  ❌
                </button>
              </div>
            </>
          </div>
        ))}
      </div>
    </div>
  );
}

export default AdminVacations;
