import "./AddVacation.css";
import { SyntheticEvent, useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import { useNavigate, useParams } from "react-router-dom";
import VacationModel from "../../../Models/VacationModel";
import vacationService from "../../../Utils/VacationServices";
import { kMaxLength } from "buffer";

function AddVacation(): JSX.Element {
  const { register, handleSubmit } = useForm<VacationModel>();
  const [vacationData, setData] = useState<any>();

  const navigate = useNavigate();
  const params = useParams();
  const vacation_id = +(params.id || "");

  const handleChange = (args: SyntheticEvent) => {
    const temp = { ...vacationData };
    const inputName = (args.target as HTMLInputElement).name;
    const inputValue = (args.target as HTMLInputElement).value;

    temp[inputName] = inputValue;
    setData(temp);
    console.log(temp);
  };

  const send = async (vacation: VacationModel) => {
    if (vacation_id > 0) {
      try {
        await vacationService.updateVacation(vacationData, vacation_id);
        navigate("/admin/vacations");
      } catch (err: any) {
        alert(err.message);
      }
    } else {
      try {
        await vacationService.addVacation(vacation);
        navigate("/admin/vacations");
      } catch (err: any) {
        alert(err.message);
      }
    }
  };

  const getSingleData = async (id: number) => {
    let myData = await vacationService.getSingleVacation(id);
    myData["date_from"] = myData["date_from"].toLocaleString().split("T")[0];
    myData["date_to"] = myData["date_to"].toLocaleString().split("T")[0];
    console.log(myData);
    setData(myData);
  };

  useEffect(() => {
    console.log(vacation_id);
    if (vacation_id > 0) {
      getSingleData(vacation_id);
    }
  }, []);

  console.log(new Date());

  return (
    <div className="AddVacation">
      <form onSubmit={handleSubmit(send)}>
        <h2>New Vacation</h2>

        <label>Destination</label>
        <input
          type="text"
          {...register("destination")}
          defaultValue={vacationData?.destination}
          onBlur={handleChange}
          required
        />

        <label>Description</label>
        <input
          type="text"
          maxLength={500}
          {...register("description")}
          defaultValue={vacationData?.description}
          onBlur={handleChange}
          required
        />

        <label>From</label>
        <input
          type="date"
          {...register("date_from")}
          defaultValue={vacationData?.date_from}
          onBlur={handleChange}
          required
        />

        <label>to</label>
        <input
          type="date"
          {...register("date_to")}
          defaultValue={vacationData?.date_to}
          onBlur={handleChange}
          required
        />

        <label>Price</label>
        <input
          type="number"
          {...register("price")}
          defaultValue={vacationData?.price}
          onBlur={handleChange}
          required
        />

        <label>Image</label>
        <input
          type="string"
          {...register("image")}
          defaultValue={vacationData?.image}
          onBlur={handleChange}
          required
        />
        <button>Add Vacation</button>
      </form>
    </div>
  );
}

export default AddVacation;
